import { createBlock } from '@wordpress/blocks';

const transforms = {
    from: [
        {
            type: 'block',
            isMultiBlock: true, //       
            blocks: ['core/image'], //     
            transform: (items) => {
                //      
                const innerBlocks = items.map((img) => {
                    return createBlock('create-block/child-block', {
                        url: img.url,
                        alt: img.alt,
                        id: img.id,
                        name: 'Team Member Name', //  
                        bio: 'Write something about them...', //  
                        socialLinks: [] 
                    });
                });

                //     
                return createBlock(
                    'create-block/faq-accordion',
                    { column: items.length }, //       
                    innerBlocks
                );
            },
        },
    ],
};

export default transforms;